<template>
  <div class="securitylists-text">
    <p>
      <input class="search-text" type="text"
             placeholder="请输入搜索的 CVE ID" v-model="cveKey">
      <button class="search-btn btn" @click="handleSearchCve">搜索</button>
    </p>
  </div>
</template>

<script type="text/ecmascript-6">
  import * as apiScanner from '@/api/transactionScaner2'

  import {
    mapGetters
  } from 'vuex'

  export default {
    data() {
      return {
        cveKey: ''
      }
    },
    computed: {
      ...mapGetters(['requestSecurity'])
    },
    methods: {
      handleSearchCve() {
        this.$store.commit('setPaginationFrom', 0)
        let str = this.cveKey
        str = str.replace(/\s+/g, ' ')
        str = str.replace(/[ ]/g, ',')
        let arr = str.split(',')
          this.$store.commit('setSearch', arr)
          apiScanner.fetchSecurity(this.requestSecurity).then(res => {
          this.$store.commit('setCveList', res.data.data.docs)
          this.$store.commit('setPaginationTotal', res.data.data.count)
        })
      }
    },
    mounted() {
      window.addEventListener('keyup', event => {
        if (event.keyCode === 13) {
          this.handleSearchCve()
        }
      })
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .securitylists-text
    p
      display inline-block
      .search-text
        width: 300px
        height: 35px
        border: #5286F7 1px solid
        color: #808080
        padding: 0 0 0 15px
        font-size: 14px
        margin: 30px 0 30px 0
        display: inline-block
      .search-btn
        height 38px
        width 60px
        background: #3E7AF6
        border: 0px
        color: #FFFFFF
        font-size: 14px
        line-height: 32px
        margin-left -3px

  .el-select
    width 120px
</style>



