<template>
  <div class="security-table">
    <div class="ibox-content">
      <el-table :data="cveList" @sort-change="handleSort">
        <el-table-column prop="vulnerability" label="CVE ID" sortable="custom"
                         show-overflow-tooltip align="center" min-width="30">
          <template slot-scope="props">
            <i>{{props.row.cveId}}</i>
          </template>
        </el-table-column>
        <el-table-column prop="severity" label="严重程度" sortable
                         show-overflow-tooltip align="center" min-width="30">
          <template slot-scope="props">
            <i>{{props.row.level}}</i>
          </template>
        </el-table-column>
        <el-table-column label="漏洞包"
                         show-overflow-tooltip align="center" min-width="40">
          <template slot-scope="props">
            <i>{{props.row.version}}</i>
          </template>
        </el-table-column>
        <el-table-column label="CVE-描述"
                         show-overflow-tooltip align="center" min-width="100">
          <template slot-scope="props">
            <i>{{props.row.description}}</i>
          </template>
        </el-table-column>
        <el-table-column label="CVE-Link"
                         show-overflow-tooltip align="center" min-width="40">
          <template slot-scope="props">
            <a :href="props.row.link" target="_blank">{{props.row.link}}</a>
          </template>
        </el-table-column>
      </el-table>
      <section class="pagination">
        <el-pagination background @size-change="handleSizeChange"
          @current-change="handleCurrentChange" :page-sizes="[10, 20, 30, 40]"
          :page-size="100" layout="total, sizes, prev, pager, next, jumper"
          :total="paginationTotalScanner">
        </el-pagination>
      </section>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import * as scanerApi from '@/api/transactionScaner2'
  import { mapGetters } from 'vuex'

  export default {
    computed: {
      ...mapGetters(['requestSecurity', 'cveList', 'paginationTotalScanner'])
    },
    methods: {
      handleFetchSecurity() {
        scanerApi.fetchSecurity(this.requestSecurity).then(res => {
          this.$store.commit('setCveList', res.data.data.docs)
          this.$store.commit('setPaginationTotal', res.data.count)
        })
      },
      handleSizeChange(val) {
        this.$store.commit('setPaginationSize', val)
        this.handleFetchSecurity()
      },
      handleCurrentChange(val) {
        this.$store.commit('setPaginationFrom', (val - 1) * this.requestSecurity.pagination.size)
        this.handleFetchSecurity()
      },
      // 排序
      handleSort(val) {
        // 将字段传入
        this.$store.commit('setSortField', val.prop)
        console.log('val.prop', val.prop)
        // 将排序方式传入
        this.$store.commit('setSortOrder', val.order.substr(0, val.order.length - 6))
        this.handleFetchSecurity()
      }
    },
    created() {
      let data = this.$route.query
      this.$store.commit('setRepository', data.repository)
      this.$store.commit('setImage', data.image)
      this.$store.commit('setTag', data.tag)
      this.handleFetchSecurity()
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .pagination
    float right
    margin 20px
</style>



