<template>
  <section class="security">
    <h2>常见的漏洞和暴露(CVE)摘要</h2>
    <div class="data-statistics" v-if="securityList">
      <el-row :gutter="18">
        <el-col :span="5">
          <div class="grid-content">
            <div class="security-important">
              <p>
                <i class="iconfont icon-system-fn-warning security-warning icon-red">
                  &#xe638;</i>低危漏洞</p>
              <p>{{securityList.high}}</p>
            </div>
          </div>
        </el-col>
        <el-col :span="5">
          <div class="grid-content bg-purple">
            <div class="security-middle">
              <p>
                <i class="iconfont icon-system-fn-warning security-warning icon-orange">
                  &#xe638;</i>中间漏洞</p>
              <p>{{securityList.medium}}</p>
            </div>
          </div>
        </el-col>
        <el-col :span="5">
          <div class="grid-content">
            <div class="security-highest">
              <p>
                <i class="iconfont icon-system-fn-warning security-warning icon-yellow">
                  &#xe638;</i>低危漏洞</p>
              <p>{{securityList.low}}</p>
            </div>
          </div>
        </el-col>
        <el-col :span="5">
          <div class="grid-content">
            <div class="security-lowest">
              <p>
                <i class="iconfont icon-system-fn-warning security-warning icon-blue">
                  &#xe638;</i>轻微漏洞</p>
              <p>{{securityList.negligible}}</p>
            </div>
          </div>
        </el-col>
        <el-col :span="4">
          <div class="grid-content">
            <div class="security-unknown">
              <p>
                <i class="iconfont icon-docker index-warning security-warning icon-black">
                  &#xe638;</i>未知漏洞</p>
              <p>{{securityList.unknown}}</p>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
  </section>
</template>

<script type="text/ecmascript-6">
  import * as scanerApi from '@/api/transactionScaner2'
  import {
    mapGetters
  } from 'vuex'

  export default {
    data() {
      return {
        securityList: ''
      }
    },
    computed: {
      ...mapGetters(['requestSecurity'])
    },
    methods: {
      handleFetchSecurity() {
        let data = this.$route.query
        scanerApi.fetchImageDetail(data).then(res => {
          this.securityList = res.data.data
        })
      }
    },
    created() {
      this.handleFetchSecurity()
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .data-statistics
    margin: 45px 0 0 0
    padding: 0 0 50px 0
    border-bottom: #E5E5E5 1px solid
    div[class^="security"]
      padding: 25px 0 0 0
      p
        text-align: center
        color: #666666
        font-size: 1.3em
        margin: 0 0 0 -20px
        i
          display: inline-block
          margin: 0 15px 0 0
      p:last-child
        position: relative
        text-align left
        left: 40%
        color: #4D4D4D
        font-size: 2.0em
        font-weight: 600
        line-height: 40px
        margin: 0

  .grid-content
    height: 92px;
    background: #FAFAFA

  .security-warning
    font-size: 1.45em

  .icon-red
    color: #D81E06

  .icon-orange
    color: #F27935

  .icon-yellow
    color: #F9BF3B

  .icon-blue
    color: #08B1D5

  .icon-black
    color: #1A1A1A
</style>



